const sale_lists = document.querySelector('.sale_lists');
const wrapper = document.querySelector('.wrapper');
const block = document.querySelector('.block');
const block_img = document.querySelector('.block_img');
const block_back = document.querySelector('.block_back');
import ggb from '../json/main_back';
const g = ggb.map((a) => {
		return `<li class="sale_list_item"><a ><img src="${a.img}" id=${a.id} width='600' alt="">
		<h3>Your products</h3>
		<p class="sale_price">${a.price_with_sales}
		 <span>${a.price}</span></p></a></li>`;
}).join("");
sale_lists.innerHTML = g;
ggb.map((a) => {
	sale_lists.addEventListener('click', (e) => {
		if(e.target.nodeName !== "IMG") {
			return;
		}
		if(a.id === e.target.id) {
			window.scrollTo({
				top: 0,  // это координат где должен быть экран
				behavior: 'smooth'  //Это анимация
			})
			wrapper.style.display = 'none';
			block.style.display = 'block';
			block_img.src = a.img;

		}
	})
})
block_back.addEventListener('click', (e) => {
	e.preventDefault();
	wrapper.style.display = 'block';
	block.style.display = 'none';
})
